@import url(https://fonts.googleapis.com/css?family=Open+Sans:300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  width: 100%;
  height: 100%
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  width: 100%;
  height: 100%;
}

#root {
  font-family: 'Open Sans', sans-serif;
  --coral: #F87060;
  --background: #FEFEFE;
  --background-alt: #DDD;
  --text: #222222;
  height: 100%;
  width: 100%;
  font-size: 1em;
}

#body {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  margin-top: 5vh;
  height: 95vh;
  overflow-x: hidden;
  overflow-y: visible;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#landing-background {
  min-height: 60%;
  max-height: 80%;
  width: 100%;
  background-image: url(/chars-website/static/media/landing.4d3d4e7c.JPG);
  background-attachment: fixed;
  background-size: cover;
  z-index: -100;
  position: fixed;
  top: 0;
  left: 0;
}

a {
  color: var(--coral);
}

a:hover {
  color: var(--text);
}


@media (max-width: 1024px) {
  #root {
    font-size: .9em;
  }
}
#toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 5vh;
  width: 30px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
  margin-right: 2vw;
}

#toggle-button:focus {
  outline: none;
}

.toggle-line {
  width: 100%;
  height: 5px;
  background: #efefef;
}

#toggle-button:hover .toggle-line {
  background: var(--background);
}

@media (min-width: 678px) {
  #toggle-button {
    display: none;
  }
}

.side-bar {
  height: 100%;
  width: 60%;
  max-width: 300px;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 3;
  background-color: #EFEFEF;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  -webkit-transition: transform 0.3s ease-out;
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side-bar.open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.side-item {
  font-size: 1.2em;
  z-index: 200;
  cursor: pointer;
  width: 100%;
  text-align: center;
  margin-top: 1vh;
}

.side-item > a {
  font-size: 1.2em;
  z-index: 200;
  cursor: pointer;
  width: 100%;
  text-align: center;
  margin-top: 1vh;
  text-decoration: none;
  color: black;
}

.side-item > a:hover {
  color: var(--coral);
}

.backdrop {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0,0,0,0.3);
  z-index: 2;
}
#nav {
  position: fixed;
  width: 100vw;
  top: 0;
  left: 0;
  height: 5vh;
  display: flex;
  align-items: center;
  z-index: 200;
  background: transparent;
}

#navbrand {
  height: 90%;
  cursor: pointer;
  text-decoration: none;
  margin-left: 1vw;
  color: var(--background);
}

#navbrand > img {
  max-width: 100%;
  max-height: 100%;
}

#spacer {
  flex: 1 1;
}

#nav-items {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0;
}

.nav-item {
  padding: 0 2rem;
  text-decoration: none;
  font-size: 1.2em;
  z-index: 200;
  cursor: pointer;
}

.nav-item > a {
  text-decoration: none;
  color: var(--background);
  z-index: 100;
  font-weight: bold;
}

.nav-item > a:hover {
  color: var(--coral);
}

#contact > a {
  border: 2px solid var(--font-nav);
  border-radius: 5px;
  padding: 3%;
}

#contact:hover > a {
  border-color: var(--font-nav-active);
}

@media (max-width: 678px) {
  #nav-items {
    display: none;
  }
}

#landing {
  min-height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-content: center;
}

#landing-img-container {
  width: 100%;
  min-height: 55vh;
  overflow: hidden;
  position: relative;
  background: transparent;
}

#landing-body {
  border-top: 3px solid var(--background-alt);
  width: 100%;
  min-height: 45vh;
  background: var(--background);
}

#landing-img-overlay {
  text-align: center;
  font-size: 6em;
  font-weight: bold;
  color: var(--background);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-shadow: 4px 4px black;
  font-family: 'Oswald', sans-serif;
  text-transform: uppercase;
}

#landing-info {
  width: 60%;
  margin: 0 auto;
}

.col-section-3 {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.col-3 {
  width: 30%;
  min-width: 275px;
  padding: 1%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.col-title {
  width: 100%;
  /*border-bottom: 4px solid black;*/
  /*border-top: 4px solid black;*/
  font-size: 2em;
  font-weight: bold;
  padding-left: 5px;
  text-align: center;
  -webkit-text-decoration: underline var(--text);
          text-decoration: underline var(--text);
  padding-bottom: 5px;
}

.col-body {
  width: 100%;
  height: 87%;
  font-family: Courier;
}
#announcements {
  list-style-type: circle;
  text-align: left;
  margin: 0;
}

.hour {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  text-align: center;
  padding-left: 30px;
  line-height: 2em;
}

.hour > div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
}
.links-bar {
  width: 100%;
  text-align: left;
  height: 24px;
}

.link {
  width: 24px;
  height: 24px;
  margin-right: 5px;
}

.link > img {
  width: 24px;
  height: 24px;
}
.contact-section {
  padding-left: 30px;
}
.section {
  min-height: 100%;
  min-height: -webkit-fill-available;
  width: 100%;
  margin: auto;

  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: flex-start;

  background: var(--background);
}

.section-title {
  font-size: 4em;
  font-weight: bold;
  text-align: center;
  height: 10%;
  margin-top: 1%;
}

.section-body {
  min-width: 50%;
  max-width: 90%;
  width: 60%;
  padding-bottom: 8%;
  padding-top: 2%;
  margin: 0 auto;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}



.body-text {
  text-align: center;
  margin-bottom: 1%;
}

#about-img {
  min-width: 300px;
  max-width: 500px;
  height: auto;
  border-radius: 5%;
  overflow: hidden;
}

#about-img > img {
  width: 100%;
  height: 100%;
}

.menu-item {
  display: flex;
  flex-direction: row;
  align-content: flex-start;
  width: 100%;
  border-top: 2px dotted var(--background-alt);
}

.menu-item:hover {
  font-weight: bolder;
}

.name-desc {
  text-align: left;
  display: flex;
  flex-direction: column;
  max-width: 75%;
}

.item-name {
  font-weight: bold;
  font-size: 1.2em;
}


.menu-section {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
}

.menu-section-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  max-width: 700px;
  text-align: center;
}

.menu-section-title-subtitle {
  margin: 0 auto 1%;
}


.menu-section-title {
  font-weight: bold;
  font-size: 2em;
}

.menu-section-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 700px;
}

.menu-spacer {
  flex-grow: 1;
}
.sliding-menu {
  display: flex;
  width: 100%;
  height: auto;
  flex-direction: column;
}

.menu-header {
  position: relative;
  height: 20%;
  min-height: 1.625em;
  width: 100%;
}


.menu-links {
  position: relative;
  display: flex;
  height: auto;
  flex-direction: row;
  margin: auto;
  width: 50%;
}

.menu-link {
  font-size: 1.5em;
  display: flex;
  justify-content: center;
  align-content: center;
  height: 100%;
  padding: 1%;
  cursor: pointer;
}

.menu-link:hover {
  opacity: .5;
  background-color: #cdcdcd;
}

.menu-bar {
  background: #dedede;
  position: absolute;
  bottom: 0;
  right: 0;
}

.menu-bar {
  width: 100%;
  height: .125em;
}

.menu-selected {
  margin: 0;
  position: absolute;
  background-color: #F87060;
  -webkit-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
  height: .125em;
  bottom: 0;
  z-index: 100;
}


.menu-content {
  width: 100%;
  height: 80%;
}

.unrolled-title {
  font-weight: bold;
  font-size: 2em;
}

.unrolled-title:hover {
  background: transparent;
  cursor: auto;
  color: var(--text);
  opacity: 1;
}




#map {
  min-height: 60vh;
  width: 100%;
}

#where > .section-body {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

#where > .section-body > div {
  padding: 1%;
  text-align: center;
}
